/* ==========================================================================
   1. Icons - Sets up the icon font and respective classes
   ========================================================================== */

/* Import the font file with the icons in it */
@font-face {
  font-family: "casper-icons";
  src: url("../fonts/casper-icons.eot?v=1");
  src: url("../fonts/casper-icons.eot?v=1#iefix") format("embedded-opentype"), url("../fonts/casper-icons.woff?v=1") format("woff"), url("../fonts/casper-icons.ttf?v=1") format("truetype"), url("../fonts/casper-icons.svg?v=1#icons") format("svg");
  font-weight: normal;
  font-style: normal;
}


/* Apply these base styles to all icons */

[class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "casper-icons", "Open Sans", sans-serif;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  text-decoration: none !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Each icon is created by inserting the correct character into the
   content of the :before pseudo element. Like a boss. */

.icon-ghost:before {
  content: "\f600";
}

.icon-feed:before {
  content: "\f601";
}

.icon-twitter:before {
  content: "\f602";
  font-size: 1.1em;
}

.icon-google-plus:before {
  content: "\f603";
}

.icon-facebook:before {
  content: "\f604";
}

.icon-arrow-left:before {
  content: "\f605";
}

.icon-stats:before {
  content: "\f606";
}

.icon-location:before {
  content: "\f607";
  margin-left: -3px;
  /* Tracking fix */
}

.icon-link:before {
  content: "\f608";
}

.icon-menu:before {
  content: "\f609";
}

/*
    IMPORTANT: When making any changes to the icon font, be sure to increment
    the version number by 1 in the @font-face rule. `?v=1` becomes `?v=2`
    This forces browsers to download the new font file.
*/

