/* ==========================================================================
   3. Utilities - These things get used a lot
   ========================================================================== */

/* Clears shit */

.clearfix {
  &:before {
    content: " ";
    display: table;
  }
  &:after {
    content: " ";
    display: table;
    clear: both;
  }
  zoom: 1;
}

/* Hides shit */

.hidden {
  text-indent: -9999px;
  visibility: hidden;
  display: none;
}

/* Creates a responsive wrapper that makes our content scale nicely */

.inner {
  position: relative;
  width: 80%;
  max-width: 710px;
  margin: 0 auto;
}

/* Centres vertically yo. (IE8+) */

.vertical {
  display: table-cell;
  vertical-align: middle;
}

/* Wraps the main content & footer */

.site-wrapper {
  position: relative;
  z-index: 10;
  min-height: 100%;
  background: #fff;
  -webkit-transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
}

body.nav-opened .site-wrapper {
  overflow-x: hidden;
  -webkit-transform: translate3D(-240px, 0, 0);
  -ms-transform: translate3D(-240px, 0, 0);
  transform: translate3D(-240px, 0, 0);
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
}

/* ==========================================================================
   4. General - The main styles for the the theme
   ========================================================================== */

/* Big cover image on the home page */

.main-header {
  position: relative;
  display: table;
  width: 100%;
  height: 50vh;
  margin-bottom: 5rem;
  text-align: center;
  background: #222 no-repeat center center;
  background-size: cover;
  overflow: hidden;
  .inner {
    width: 80%;
  }
}

.main-nav {
  position: relative;
  padding: 35px 40px;
  margin: 0 0 30px 0;
  a {
    text-decoration: none;
    font-family: 'Open Sans', sans-serif;
  }
}

.no-cover .main-nav {
  position: relative;
  padding: 5px 40px;
  margin: 0 0 0 0;
  background: black;
  color: white;
  a {
    text-decoration: none;
    font-family: 'Open Sans', sans-serif;
  }
}


/* Navigation */

body.nav-opened .nav-cover {
  position: fixed;
  top: 0;
  left: 0;
  right: 240px;
  bottom: 0;
  z-index: 200;
}

.nav {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 5;
  width: 240px;
  opacity: 0;
  background: #111;
  margin-bottom: 0;
  text-align: left;
  overflow-y: auto;
  -webkit-transition: -webkit-transform 0.5s ease, opacity 0.3s ease 0.7s;
  transition: transform 0.5s ease, opacity 0.3s ease 0.7s;
}

.nav-top {
  background: #111;
}

body {
  &.nav-closed .nav {
    -webkit-transform: translate3D(97px, 0, 0);
    -ms-transform: translate3D(97px, 0, 0);
    transform: translate3D(97px, 0, 0);
  }
  &.nav-opened .nav {
    opacity: 1;
    -webkit-transition: -webkit-transform 0.3s ease, opacity 0s ease 0s;
    transition: transform 0.3s ease, opacity 0s ease 0s;
    -webkit-transform: translate3D(0, 0, 0);
    -ms-transform: translate3D(0, 0, 0);
    transform: translate3D(0, 0, 0);
  }
}

.nav-title {
  position: absolute;
  top: 45px;
  left: 30px;
  font-size: 16px;
  font-weight: 100;
  text-transform: uppercase;
  color: #fff;
}

.nav-close {
  position: absolute;
  top: 38px;
  right: 25px;
  width: 20px;
  height: 20px;
  padding: 0;
  font-size: 10px;
  &:focus {
    outline: 0;
  }
  &:before, &:after {
    content: '';
    position: absolute;
    top: 0;
    width: 20px;
    height: 1px;
    background: rgb(150, 150, 150);
    top: 15px;
    -webkit-transition: background 0.15s ease;
    transition: background 0.15s ease;
  }
  &:before {
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  &:after {
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    transform: rotate(-45deg);
  }
  &:hover {
    &:before, &:after {
      background: rgb(255, 255, 255);
    }
  }
}

.nav, .nav-top {
  ul {
    padding: 90px 9% 5%;
    list-style: none;
    counter-reset: item;
  }
  li {
    &:before {
      display: block;
      float: right;
      padding-right: 4%;
      padding-left: 5px;
      text-align: right;
      font-size: 1.2rem;
      vertical-align: bottom;
      color: #B8B8B8;
      content: counter(item, lower-roman);
      counter-increment: item;
    }
    margin: 0;
    a {
      text-decoration: none;
      line-height: 1.4;
      font-size: 1.4rem;
      display: block;
      padding: 0.6rem 4%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      &:after {
        display: inline-block;
        content: " .......................................................";
        color: rgba(255, 255, 255, 0.2);
        margin-left: 5px;
      }
    }
  }
  .nav-current {
    &:before {
      color: #fff;
    }
    a:after {
      content: " ";
      border-bottom: rgba(255, 255, 255, 0.5) 1px solid;
      width: 100%;
      height: 1px;
    }
  }
  a {
    &:link, &:visited {
      color: #B8B8B8;
    }
  }
  li.nav-current a {
    color: #fff;
  }
  a {
    &:hover, &:active, &:focus {
      color: #fff;
    }
  }
}

.subscribe-button {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  display: block;
  position: absolute;
  bottom: 30px;
  left: 30px;
  right: 30px;
  height: 38px;
  padding: 0 20px;
  color: #111 !important;
  /* Overides `.nav a:link, .nav a:visited` colour */
  text-align: center;
  font-size: 12px;
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  line-height: 35px;
  border-radius: 3px;
  background: #fff;
  transition: all ease 0.3s;
  &:before {
    font-size: 9px;
    margin-right: 6px;
  }
}

.coffee-button {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  display: block;
  position: absolute;
  bottom: 76px;
  left: 30px;
  right: 30px;
  height: 38px;
  padding: 0;
  color: #111 !important;
  /* Overides `.nav a:link, .nav a:visited` colour */
  text-align: center;
  font-size: 12px;
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  line-height: 35px;
  border-radius: 3px;
  // background: #fff;
  transition: all ease 0.3s;
  &:before {
    font-size: 9px;
    margin-right: 6px;
  }
}

/* Create a bouncing scroll-down arrow on homepage with cover image */

.scroll-down {
  display: block;
  position: absolute;
  z-index: 100;
  bottom: 45px;
  left: 50%;
  margin-left: -16px;
  width: 34px;
  height: 34px;
  font-size: 34px;
  text-align: center;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.7);
  -webkit-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  transform: rotate(-90deg);
  -webkit-animation: bounce 4s 2s infinite;
  animation: bounce 4s 2s infinite;
  &:hover {
    color: #fff;
    -webkit-animation: none;
    animation: none;
  }
}

/* Stop it bouncing and increase contrast when hovered */

/* Put a semi-opaque radial gradient behind the icon to make it more visible
   on photos which happen to have a light background. */

.home-template .main-header:after {
  display: block;
  content: " ";
  width: 150px;
  height: 130px;
  border-radius: 100%;
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -75px;
  background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0.15) 0%, rgba(0, 0, 0, 0) 70%, rgba(0, 0, 0, 0) 100%);
}

/* Hide when there's no cover image or on page2+ */

.no-cover {
  .scroll-down, &.main-header:after {
    display: none;
  }
}

.archive-template {
  .scroll-down, .main-header:after {
    display: none;
  }
}

/* Appears in the top left corner of your home page */

.blog-logo {
  display: block;
  float: left;
  background: none !important;
  /* Makes sure there is never a background */
  border: none !important;
  /* Makes sure there is never a border */
  img {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    display: block;
    height: 38px;
    padding: 1px 0 5px 0;
    width: auto;
  }
}

.menu-button {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  display: inline-block;
  float: right;
  height: 38px;
  padding: 0 15px;
  border-style: solid;
  border-width: 1px;
  opacity: 1;
  text-align: center;
  font-size: 12px;
  text-transform: uppercase;
  line-height: 35px;
  white-space: nowrap;
  border-radius: 3px;
  transition: all 0.5s ease;
  &:before {
    font-size: 12px;
    font-weight: bold;
    margin-right: 6px;
    position: relative;
    top: 1px;
  }
  &:hover {
    background: #fff;
  }
  &:focus {
    outline: 0;
  }
}

/* When the navigation is closed */

.nav-closed {
  .menu-button {
    color: #fff;
    border-color: rgba(255, 255, 255, 0.6);
    &:hover {
      color: #222;
    }
  }
  .no-cover .menu-button {
    border-color: #BFC8CD;
    color: #9EABB3;
    &:hover {
      border-color: #555;
      color: #555;
    }
  }
}

/* When the navigation is closed and there is no cover image */

/* When the navigation is opened */

.nav-opened .menu-button {
  padding: 0 12px;
  background: #111;
  border-color: #111;
  color: #fff;
  -webkit-transform: translate3D(94px, 0, 0);
  -ms-transform: translate3D(94px, 0, 0);
  transform: translate3D(94px, 0, 0);
  transition: all 0.3s ease;
  .word {
    opacity: 0;
    transition: all 0.3s ease;
  }
}

/* Special styles when overlaid on an image*/

.main-nav.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  border: none;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 100%);
}

.no-cover .main-nav.overlay {
  background: none;
}

/* The details of your blog. Defined in ghost/settings/ */

.page-title {
  margin: 10px 0 10px 0;
  font-size: 5rem;
  letter-spacing: -1px;
  font-weight: 700;
  font-family: "Open Sans", sans-serif;
  color: #fff;
}

.page-description {
  margin: 0;
  font-size: 2rem;
  line-height: 1.5em;
  font-weight: 400;
  font-family: "Merriweather", serif;
  letter-spacing: 0.01rem;
