body:not(.post-template) .post-title {
  font-size: 3.8rem;
  text-transform: none;
  margin-bottom: 0.1rem;
}

.post-title a {
  text-decoration: none;
}

.post-excerpt p {
  margin: 0;
  font-size: 0.9em;
  line-height: 1.7em;
}

.read-more {
  text-decoration: none;
}

.post-meta {
  display: block;
  margin: 0.5rem 0 1.75rem 0;
  font-family: "Open Sans", sans-serif;
  font-size: 1.5rem;
  line-height: 2.2rem;
  color: #9EABB3;
}

.author-thumb {
  width: 24px;
  height: 24px;
  float: left;
  margin-right: 9px;
  border-radius: 100%;
}

.post-meta a {
  color: #9EABB3;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}

.user-meta {
  position: relative;
  padding: 0.3rem 40px 0 100px;
  min-height: 77px;
}

.post-date {
  display: inline-block;
  margin-left: 8px;
  padding-left: 12px;
  border-left: #d5dbde 1px solid;
  text-transform: uppercase;
  font-size: 1.3rem;
  white-space: nowrap;
}

.user-image {
  position: absolute;
  top: 0;
  left: 0;
}

.user-name {
  display: block;
  font-weight: 700;
}

.user-bio {
  display: block;
  max-width: 440px;
  font-size: 1.4rem;
  line-height: 1.5em;
}

.publish-meta {
  position: absolute;
  top: 0;
  right: 0;
  padding: 4.3rem 0 4rem 0;
  text-align: right;
}

.publish-heading {
  display: block;
  font-weight: 700;
}

.publish-date {
  display: block;
  font-size: 1.4rem;
  line-height: 1.5em;
}

/* ==========================================================================
   5. Single Post - When you click on an individual post
   ========================================================================== */

.post-template {
  .post-header {
    margin-bottom: 3.4rem;
  }
  .post-title {
    margin-bottom: 0;
  }
  .post-meta {
    margin: 0;
  }
  .post-date {
    padding: 0;
    margin: 0;
    border: none;
  }
  .content {
    overflow: hidden;
  }
  .post {
    margin-top: 0;
    border-bottom: none;
    padding-bottom: 0;
    &:after {
      display: none;
    }
  }
}

/* Stop elements, such as img wider than the post content, from
   creating horizontal scroll - slight hack due to imperfections
   with browser width % calculations and rounding */

/* Tweak the .post wrapper style */

/* Kill that stylish little circle that was on the border, too */

/* Keep images centered, and allow images wider than the main
   text column to break out. */

.post-content img {
  display: block;
  max-width: 126%;
  height: auto;
  padding: 0.6em 0;
  /* Centers an image by (1) pushing its left edge to the
     center of its container and (2) shifting the entire image
     in the opposite direction by half its own width.
     Works for images that are larger than their containers. */
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
  /* for Safari and iOS */
  -ms-transform: translateX(-50%);
  /* for IE9 */
  transform: translateX(-50%);
}

.footnotes {
  font-style: italic;
  font-size: 1.3rem;
  line-height: 1.6em;
  li {
    margin: 0.6rem 0;
  }
  p {
    margin: 0;
    a:last-child {
      text-decoration: none;
    }
  }
}

/* The author credit area after the post */

.post-footer {
  position: relative;
  margin: 6rem 0 0 0;
  padding: 6rem 0 0 0;
  border-top: #EBF2F6 1px solid;
  h4 {
    font-size: 1.8rem;
    margin: 0;
  }
  p {
    margin: 1rem 0;
    font-size: 1.4rem;
    line-height: 1.75em;
  }
}

/* list of author links - location / url */

.author-meta {
  padding: 0;
  margin: 0;
  list-style: none;
  font-size: 1.4rem;
  line-height: 1;
  font-style: italic;
  color: #9EABB3;
  a {
    color: #9EABB3;
    &:hover {
      color: #111;
    }
  }
}

/* Create some space to the right for the share links */

.post-footer {
  .author {
    margin-right: 180px;
  }
  h4 a {
    color: #2e2e2e;
    text-decoration: none;
    &:hover {
      text-decoration: underline;
    }
  }
  .share {
    position: absolute;
    top: 6rem;
    right: 0;
    width: 140px;
    a {
      font-size: 1.8rem;
      display: inline-block;
      margin: 1rem 1.6rem 1.6rem 0;
      color: #BBC7CC;
      text-decoration: none;
    }
    .icon-twitter:hover {
      color: #55acee;
    }
    .icon-facebook:hover {
      color: #3b5998;
    }
    .icon-google-plus:hover {
      color: #dd4b39;
    }
  }
}

/* Drop the share links in the space to the right.
   Doing it like this means it's easier for the author bio
   to be flexible at smaller screen sizes while the share
   links remain at a fixed width the whole time */

/* ==========================================================================
   6. Author profile
   ========================================================================== */

.post-head.main-header {
  height: 45vh;
  min-height: 180px;
}

.no-cover.post-head.main-header {
  height: 85px;
  min-height: 0;
  margin-bottom: 0;
  background: transparent;
}

.tag-head.main-header, .author-head.main-header {
  height: 40vh;
  min-height: 180px;
}

.no-cover.author-head.main-header {
  height: 10vh;
  min-height: 100px;
  background: transparent;
}

.author-profile {
  padding: 0 15px 5rem 15px;
  border-bottom: #EBF2F6 1px solid;
  text-align: center;
  &:after {
    display: block;
    content: "";
    width: 7px;
    height: 7px;
    border: #E7EEF2 1px solid;
    position: absolute;
    bottom: -5px;
    left: 50%;
    margin-left: -5px;
    background: #FFF;
    border-radius: 100%;
    box-shadow: #FFF 0 0 0 5px;
  }
}

/* Add a little circle in the middle of the border-bottom */

.author-image {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  display: block;
  position: absolute;
  top: -40px;
  left: 50%;
  margin-left: -40px;
  width: 80px;
  height: 80px;
  border-radius: 100%;
  overflow: hidden;
  padding: 6px;
  background: #fff;
  z-index: 2;
  box-shadow: #E7EEF2 0 0 0 1px;
  .img {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center center;
    border-radius: 100%;
  }
}

.author-profile .author-image {
  position: relative;
  left: auto;
  top: auto;
  width: 120px;
  height: 120px;
  padding: 3px;
  margin: -100px auto 0 auto;
  box-shadow: none;
}

.author-title {
  margin: 1.5rem 0 1rem;
}

.author-bio {
  font-size: 1.8rem;
  line-height: 1.5em;
  font-weight: 200;
  color: #50585D;
  letter-spacing: 0;
  text-indent: 0;
  text-align: justify;
}

.author-meta {
  margin: 1.6rem 0;
}

/* Location, website, and link */

.author-profile .author-meta {
  margin: 2rem 0;
  font-family: "Merriweather", serif;
  letter-spacing: 0.01rem;
  font-size: 1.7rem;
}

.author-meta {
  span {
    display: inline-block;
    margin: 0 2rem 1rem 0;
    word-wrap: break-word;
  }
  a {
    text-decoration: none;
  }
}

/* Turn off meta for page2+ to make room for extra
   pagination prev/next links */

.archive-template .author-profile .author-meta {
  display: none;
}

/* ==========================================================================
   7. Read More - Next/Prev Post Links
   ========================================================================== */

.read-next {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: stretch;
  -webkit-align-items: stretch;
  -ms-flex-align: stretch;
  align-items: stretch;
  margin-top: 10rem;
}

.read-next-story {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
  -webkit-flex-grow: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  min-width: 50%;
  text-decoration: none;
  position: relative;
  text-align: center;
  color: #fff;
  background: #222 no-repeat center center;
  background-size: cover;
  overflow: hidden;
  &:hover {
    &:before {
      background: rgba(0, 0, 0, 0.8);
      transition: all 0.2s ease;
    }
    .post:before {
      color: #222;
      background: #fff;
      transition: all 0.2s ease;
    }
  }
  &:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    transition: all 0.5s ease;
  }
  .post {
    padding-top: 6rem;
    padding-bottom: 6rem;
    &:before {
      content: "Read This Next";
      padding: 4px 10px 5px;
      text-transform: uppercase;
      font-size: 1.1rem;
      font-family: "Open Sans", sans-serif;
      color: rgba(255, 255, 255, 0.8);
      border: rgba(255, 255, 255, 0.5) 1px solid;
      border-radius: 4px;
      transition: all 0.5s ease;
    }
  }
  &.prev .post:before {
    content: "You Might Enjoy";
  }
  h2 {
    margin-top: 1rem;
    color: #fff;
  }
  p {
    margin: 0;
    color: rgba(255, 255, 255, 0.8);
  }
  &.no-cover {
    background: #f5f8fa;
    &:before {
      display: none;
    }
    .post:before {
      color: rgba(0, 0, 0, 0.5);
      border-color: rgba(0, 0, 0, 0.2);
    }
    h2 {
      color: rgba(0, 0, 0, 0.8);
    }
    p {
      color: rgba(0, 0, 0, 0.5);
    }
    + .read-next-story.no-cover {
      border-left: rgba(0, 0, 100, 0.04) 1px solid;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
    }
  }
}

/* Special styles for posts with no cover images */

/* if there are two posts without covers, put a border between them */

/* Correctly position site-footer when next to the .read-next container */

.read-next + .site-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0;
}

/* ==========================================================================
   8. Third Party Elements - Embeds from other services
   ========================================================================== */

/* Github */

.gist {
  table {
    margin: 0;
    font-size: 1.4rem;
  }
  td {
    line-height: 1.4;
  }
  .line-number {
    min-width: 25px;
  }
}

/* Pastebin */

.content .embedPastebin {
  margin-bottom: 1.75em;
}

/* ==========================================================================
   9. Pagination - Tools to let you flick between pages
   ========================================================================== */

/* The main wrapper for our pagination links */

.pagination {
  position: relative;
  width: 80%;
  max-width: 710px;
  margin: 4rem auto;
  font-family: "Open Sans", sans-serif;
  font-size: 1.3rem;
  color: #9EABB3;
  text-align: center;
  a {
    color: #9EABB3;
    transition: all 0.2s ease;
  }
}

/* Push the previous/next links out to the left/right */

.older-posts, .newer-posts {
  position: absolute;
  display: inline-block;
  padding: 0 15px;
  border: #bfc8cd 1px solid;
  text-decoration: none;
  border-radius: 4px;
  transition: border 0.3s ease;
}

.older-posts {
  right: 0;
}

.page-number {
  display: inline-block;
  padding: 2px 0;
  min-width: 100px;
}

.newer-posts {
  left: 0;
}

.older-posts:hover, .newer-posts:hover {
  color: #889093;
  border-color: #98a0a4;
}

.extra-pagination {
  display: none;
  border-bottom: #EBF2F6 1px solid;
  &:after {
    display: block;
    content: "";
    width: 7px;
    height: 7px;
    border: #E7EEF2 1px solid;
    position: absolute;
    bottom: -5px;
    left: 50%;
    margin-left: -5px;
    background: #FFF;
    border-radius: 100%;
    box-shadow: #FFF 0 0 0 5px;
  }
  .pagination {
    width: auto;
  }
}

/* On page2+ make all the headers smaller */

.archive-template {
  .main-header {
    max-height: 30vh;
  }
  .extra-pagination {
    display: block;
  }
}

/* On page2+ show extra pagination controls at the top of post list */

/* ==========================================================================
   10. Footer - The bottom of every page
   ========================================================================== */

.site-footer {
  position: relative;
  margin: 8rem 0 0 0;
  padding: 1rem 15px;
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  line-height: 1.75em;
  color: #BBC7CC;
  a {
    color: #BBC7CC;
    text-decoration: none;
    font-weight: bold;
    &:hover {
      border-bottom: #bbc7cc 1px solid;
    }
  }
}

.poweredby {
  display: block;
  width: 45%;
  float: right;
  text-align: right;
}

.copyright {
  display: block;
  width: 45%;
  float: left;
}

