  .older-posts, .newer-posts {
    position: static;
    margin: 10px 0;
  }
  .page-number {
    display: block;
  }
  .site-footer {
    margin-top: 3rem;
  }
  .author-profile {
    padding-bottom: 2rem;
  }
  .post-head.main-header {
    height: 30vh;
  }
  .tag-head.main-header, .author-head.main-header {
    height: 20vh;
  }
  .author-profile {
    .author-image {
      margin-top: -70px;
    }
